#granola() { .render { 
    .nav() {
    	#ui.nav();
        .@{g}nav {
            #ui.nav(nav-outer);
        }
        .@{g}nav ul {
        	#ui.nav(nav-list);
        }
        .@{g}nav li {
        	#ui.nav(nav-list-item);
        }
        .@{g}nav a {
            #ui.nav(nav-list-link);
        }
        .@{g}nav-justify {
        	#ui.nav(nav-justify);
        }
        .@{g}nav-justify-item {
        	#ui.nav(nav-justify-item);
        }
        .prefix("btn-", {
            display: inline-block;
        });
    }
    .panels() {
    	#ui.panels();
    	.@{g}page {
    		#ui.panels(page);
    	}
    }
    .type() {
    	#ui.type();
    	h1, .@{g}h1 {
    		#ui.type(h1);
    	}
    		
    }
	
}}
